.actions {
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  opacity: 0;
  margin: -10px 0;
  float: right;
}

.Thing:hover .actions {
  opacity: 1;
}

.actions button {
  border: 0;
  background: transparent;
  color: #999;
  font-size: 1.5rem;
  margin-top:6px;
}

.actions button:hover {
  color: black;
}

.actions button.promote:hover {
  color: blue;
}


